@import url("https://fonts.googleapis.com/css?family=Montserrat:400,400i,700&subset=cyrillic,cyrillic-ext");
body {
  font-family: Montserrat, Arial, sans-serif; }

body {
  background: #4B7C9C url("./assets/img/bg-calm.jpg");
  background-size: cover;
  height: 100vh; }

h1, h2, h3, h4, p, ul, li {
  margin: 0;
  padding: 0; }

button {
  cursor: pointer;
  border: none;
  background: none;
  outline: none;
  font: inherit;
  line-height: normal;
  overflow: visible;
  padding: 5px;
  -webkit-user-select: none;
  /* for button */
  -moz-user-select: none;
  -ms-user-select: none; }

input::-moz-focus-inner,
button::-moz-focus-inner {
  border: 0;
  padding: 5px;
  border: none; }

input[type="checkbox"],
input[type="radio"] {
  display: none; }

input[type="text"],
input[type="password"],
textarea {
  font-size: 14px;
  line-height: 18px;
  width: 100%;
  padding: 10px 20px;
  background: rgba(245, 239, 237, 0.7);
  border: none;
  border-radius: 5px;
  display: block;
  transition: background-color 0.3s; }
  input[type="text"]:hover,
  input[type="password"]:hover,
  textarea:hover {
    background: #f5efed; }
  input[type="text"]:focus,
  input[type="password"]:focus,
  textarea:focus {
    background: white; }
  input[type="text"]:invalid,
  input[type="password"]:invalid,
  textarea:invalid {
    border-color: #d8315b; }

input[type="checkbox"].checkbox ~ .checkbox-icon {
  color: #064789; }

input[type="checkbox"].checkbox:checked ~ .checkbox-icon--unchecked {
  display: none; }

input[type="checkbox"].checkbox:not(:checked) ~ .checkbox-icon--checked {
  display: none; }

.App {
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column; }

.page {
  height: 100%;
  display: flex;
  flex-direction: column; }

.btn {
  display: block;
  padding: 10px 20px;
  border-radius: 7px;
  box-shadow: 2px 2px 5px rgba(48, 50, 61, 0.4);
  border: none;
  outline: none; }
  .btn:focus {
    border: none;
    outline: none; }

.btn.disabled {
  opacity: 0.6; }

.btn-inline {
  display: inline-block;
  margin: 0 5px;
  padding: 5px 10px; }

.btn-primary {
  color: white;
  font-weight: 700;
  background: #0860ba;
  transition: background-color 1s ease-out; }
  .btn-primary:hover {
    background: #0a7aeb; }

.btn-secondary {
  background: #ffd866;
  transition: background-color 0.3s ease-out; }
  .btn-secondary:hover {
    background: #ffcb33; }

.translucent {
  opacity: .4; }

.noselect {
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Safari */
  -khtml-user-select: none;
  /* Konqueror HTML */
  -moz-user-select: none;
  /* Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
  /* Non-prefixed version, currently
                                  supported by Chrome and Opera */ }

.overlay {
  position: fixed;
  z-index: 90;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.7);
  animation: fadeIn 0.5s; }

@keyframes fadeIn {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes halfFadeIn {
  from {
    opacity: 0.5; }
  to {
    opacity: 1; } }

@media (max-width: 1365px) {
  #app::before {
    content: 'Mobile version is not available now. Please try again later';
    display: block;
    background: white;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 20px;
    border-radius: 5px;
    font-weight: 700; }
  .App {
    display: none; } }

.ps__thumb-y {
  right: 3px !important;
  background-color: #4d5061 !important; }

.ps__rail-y:hover > .ps__thumb-y,
.ps__rail-y:focus > .ps__thumb-y,
.ps__rail-y.ps--clicking .ps__thumb-y {
  width: 6px !important;
  background: #FDF9F3 !important; }

.oc-scrollbar-container .ps__rail-y {
  opacity: 1 !important; }

.ps__rail-y:hover,
.ps__rail-x:focus,
.ps__rail-y:focus,
.ps .ps__rail-y.ps--clicking {
  background-color: transparent !important; }

* {
  box-sizing: border-box; }

.Toastify__toast {
  font-family: inherit;
  padding: 20px;
  border-radius: 5px; }
